import * as React from "react"
import ServiceBlock from "./ServiceBlock"
import { faTruckField } from "@fortawesome/free-solid-svg-icons/faTruckField"
import { faHeadset } from "@fortawesome/free-solid-svg-icons/faHeadset"
import { faFlask } from "@fortawesome/free-solid-svg-icons/faFlask"
import { faTruckRampBox } from "@fortawesome/free-solid-svg-icons/faTruckRampBox"

const Services = ({ data }) => {
  let fa_icons = [faTruckField, faTruckRampBox, faFlask, faHeadset]
  return (
    <>
      <div className="auto-container">
        {/* <Title section="Servicios" title="Algunos de nuestros servicios" /> */}
        <div className="row clearfix">
          {data.servicios.nodes.map((item, i) => {
            return (
              <ServiceBlock
                key={i}
                title={item.title}
                text={item.description}
                icon={fa_icons[i]}
                href={item.href}
                image={item.image.childImageSharp}
              />
            )
          })}
        </div>
      </div>
    </>
  )
}

export default Services
