import * as React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"

import PageTitle from "../components/PageTitle"
import Seo from "../components/seo"
import Services from "../components/Services"
import ContentBlockPat from "../components/construct/ContentBlockPat"

export const Head = () => <Seo title="Algunos de nuestros servicios" />

const ServicesPage = ({ data }) => (
  <Layout>
    <PageTitle title="Servicios" />
    <section className="construct-service-area section_t_70 section_b_100">
      <div className="container">
        <ContentBlockPat />
        <Services data={data}></Services>
      </div>
    </section>

  </Layout>
)

export const query = graphql`
  query {
    servicios: allServiciosJson(filter: {href: {nin: "/servicios/medicion-pat"}}) {
      nodes {
        title
        description
        href
        image {
          childImageSharp {
            gatsbyImageData(
                placeholder: BLURRED
                formats: [AUTO, WEBP, AVIF]
                width: 370
                height: 188
              )
          }
        }
      }
    }
  }
`

export default ServicesPage
