import React from "react"
import { Link } from "gatsby"
import { graphql, useStaticQuery } from "gatsby"

const ContentBlockPat = () => {

    const data = useStaticQuery(graphql`
   query {
    servicio: 
  allServiciosJson(filter: {href: {eq: "/servicios/medicion-pat/"}}) {
    edges {
      node {
        id
        description
        href
        title
        menu
      }
    }
  }

  }
  `)

    return (
        <section >

            <div className="construct-about-bottom-area construct-content-block-bottom-area">
                <div className="construct-main-caption">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-8">
                                <div className="about-bottom-left section_100">
                                    <div className="about-bottom-one">
                                        <h3>{data.servicio.edges[0].node.title}</h3>
                                        <p>{data.servicio.edges[0].node.description}</p>
                                    </div>
                                    <Link to="/contacto" className="construct-btn">contactanos</Link>
                                    <Link to={data.servicio.edges[0].node.href} className="construct-btn">más información</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    )
}





export default ContentBlockPat